
.Danger {
  padding: 20px;
  background-color: #f44336;
  color: white;
}

.Success {
  padding: 20px;
  background-color: #4CAF50;
  color: white;
}

.Closebtn {
  margin-left: 15px;
  color: white;
  font-weight: bold;
  float: right;
  font-size: 22px;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.Closebtn:hover {
  color: black;
}